.method { padding-top: 160px; padding-bottom: 170px; position: relative; overflow: hidden; }

.method__background { pointer-events: none; position: absolute; left: 0; top: 0; width: 100%; height: 100%; }

.method__background .icon { transform: rotate(188deg); position: absolute; font-size: 707px; stroke: #fff; opacity: .3; bottom: -250px; right: -395px; }

.method__particle-first { right: 21px; top: -120px; }

.method__particle-second { top: 110px; right: -94px; }

.method__particle-third { top: 144px; left: 366px; }

.method__particle-fourth { top: 234px; left: -73px; }

.method__particle-fifth { top: 510px; right: 437px; }

.method__particle-sixth { top: 602px; right: -78px; }

.method__particle-seventh { top: 584px; left: 340px; }

.method__particle-eighth { left: -104px; bottom: 291px; }

.method__particle-ninth { left: -63px; bottom: 87px; }

.method__particle-tenth { left: 616px; bottom: -114px; }

.method__text-list { margin-top: 31px; display: flex; justify-content: space-between; }

@media only screen and (max-width: 1023px) { .method__text-list { flex-direction: column; } }

.method__text-item { max-width: 355px; width: 33%; }

@media only screen and (max-width: 1023px) { .method__text-item { margin-top: 16px; max-width: none; width: 100%; } }

.method__text-item br { line-height: 3.1; }

.method__video-list { margin-top: 116px; display: flex; justify-content: space-between; }

@media only screen and (max-width: 767px) { .method__video-list { margin-top: 24px; flex-wrap: wrap; } }

.method__video-item { width: calc(50% - 12px); }

@media only screen and (max-width: 767px) { .method__video-item { margin-top: 16px; width: 100%; } }

.for-whom { padding-top: 92px; padding-bottom: 120px; }

.for-whom__inner { display: flex; justify-content: space-between; }

@media only screen and (max-width: 1023px) { .for-whom__inner { flex-wrap: wrap; } }

.for-whom__background { pointer-events: none; position: absolute; left: 0; top: 0; width: 100%; height: 100%; }

.for-whom__particle-first { left: 75px; top: -57px; }

.for-whom__particle-second { top: 76px; left: 500px; }

.for-whom__particle-third { top: 391px; left: -73px; }

.for-whom__particle-fourth { bottom: 230px; left: 479px; }

.for-whom__particle-fifth { bottom: 266px; right: -99px; }

.for-whom__particle-sixth { bottom: -31px; left: -2px; }

.for-whom__particle-seventh { bottom: -42px; left: 622px; }

.for-whom__text { width: 50%; }

@media only screen and (max-width: 1023px) { .for-whom__text { width: 100%; } }

.for-whom__descr { margin-top: 32px; max-width: 440px; }

@media only screen and (max-width: 1023px) { .for-whom__descr { max-width: none; } }

.for-whom__list { margin-top: 25px; }

@media only screen and (max-width: 1023px) { .for-whom__list { display: flex; justify-content: space-between; flex-wrap: wrap; } }

.for-whom__item { max-width: 376px; margin-bottom: 43px; display: flex; align-items: center; }

@media only screen and (max-width: 1023px) { .for-whom__item { width: 49%; max-width: none; } }

@media only screen and (max-width: 767px) { .for-whom__item { width: 100%; margin-bottom: 16px; } }

.for-whom__item-text { margin-left: 20px; }

.for-whom__images { margin-left: auto; width: 49%; }

@media only screen and (max-width: 1023px) { .for-whom__images { width: 100%; } }

.for-whom__image-list { margin-top: -12px; display: flex; flex-wrap: wrap; justify-content: space-between; }

.for-whom__image-item { margin-top: 30px; width: calc(50% - 12px); }

.for-whom__image-bg { box-shadow: 0 35px 60px -24px rgba(37, 35, 35, 0.5); border-radius: 20px; min-height: 359px; background-size: cover; background-position: center; background-repeat: no-repeat; }

@media only screen and (max-width: 767px) { .for-whom__image-bg { min-height: 220px; } }

.partners { padding: 88px 0 125px; }

.partners__inner { position: relative; }

.partners__bg { position: absolute; top: -163px; left: -238px; bottom: -208px; width: 856px; background-size: contain; background-position: center; background-repeat: no-repeat; z-index: -1; }

.partners__wrap { position: relative; overflow: auto; white-space: nowrap; }

.partners__list { width: 110%; position: relative; padding-bottom: 58px; margin-top: 36px; display: inline-flex; flex-wrap: nowrap; }

.ps--active-x .partners__list { width: auto; }

.partners__item { max-width: 212px; flex-shrink: 0; }

.partners__item:not(:first-child) { margin-left: 97px; }

.partners__item img { display: block; max-width: 100%; }

.partners__particle-first { top: calc(100% + 52px); left: -93px; }

.partners__particle-second { top: 23px; left: -1px; }

.partners__particle-third { top: 36px; left: 870px; }

.ps.scrollbar .ps__rail-x { background-color: rgba(56, 56, 56, 0.2); height: 3px; border-radius: 2px; margin-bottom: 2px; }

.ps.scrollbar .ps__thumb-x { height: 7px; border-radius: 4px; background-color: #9b7de8; bottom: -2px; }

.contacts { position: relative; padding: 184px 0 187px; margin-bottom: 125px; }

.contacts__inner { position: relative; }

.contacts__content { display: flex; }

.contacts__text { padding-top: 22px; }

.contacts__block { margin-top: 19px; }

.contacts__label { opacity: 0.5; }

.contacts__value { font-size: 18px; font-weight: 700; }

.contacts__form { margin-left: 46px; }

.contacts__particle-first { top: calc(100% + 195px); left: -85px; }

.contacts__particle-second { top: calc(100% + 65px); left: 121px; }

.contacts__particle-third { top: calc(100% + 200px); left: 857px; }

