.for-whom {
	padding-top: 92px;
	padding-bottom: 120px;

	&__inner {
		display: flex;
		justify-content: space-between;
		@include md {
			flex-wrap: wrap; } }

	&__background {
		pointer-events: none;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%; }

	&__particle-first {
		left: 75px;
		top: -57px; }

	&__particle-second {
		top: 76px;
		left: 500px; }

	&__particle-third {
		top: 391px;
		left: -73px; }

	&__particle-fourth {
		bottom: 230px;
		left: 479px; }

	&__particle-fifth {
		bottom: 266px;
		right: -99px; }

	&__particle-sixth {
		bottom: -31px;
		left: -2px; }

	&__particle-seventh {
		bottom: -42px;
		left: 622px; }

	&__text {
		width: 50%;
		@include md {
			width: 100%; } }

	&__title {}

	&__descr {
		margin-top: 32px;
		max-width: 440px;
		@include md {
			max-width: none; } }

	&__list {
		margin-top: 25px;
		@include md {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap; } }

	&__item {
		max-width: 376px;
		margin-bottom: 43px;
		display: flex;
		align-items: center;
		@include md {
			width: 49%;
			max-width: none; }
		@include sm {
			width: 100%;
			margin-bottom: 16px; } }

	&__item-position {}

	&__item-text {
		margin-left: 20px; }

	&__images {
		margin-left: auto;
		width: 49%;
		@include md {
			width: 100%; } }

	&__image-list {
		margin-top: -12px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__image-item {
		margin-top: 30px;
		width: calc(50% - 12px); }

	&__image-bg {
		box-shadow: 0 35px 60px -24px rgba(37, 35, 35, 0.5);
		border-radius: 20px;
		min-height: 359px;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		@include sm {
			min-height: 220px; } } }

